
import React, { useRef } from "react";
import { updStore } from "../../../stores/Utils";
import SignatureCanvas from 'react-signature-canvas';
import { CloseModal } from "../../../components/DialogHRM";





const SignatureBox = ({data, onConfirm}) => {
	const sigPad = useRef(null);

	const clear = () =>{
		updStore("b64_signature",null);
		sigPad.current.clear()
	}

	const save = () =>{
		let b64 = sigPad.current.getTrimmedCanvas().toDataURL('image/png');
		onConfirm && onConfirm(b64)
		close();
	}

	const close  = ()  => {
		data?.modalID && CloseModal({id:data?.modalID})
	}

	return (
		<div style={{
			maxHeight: "80vh",
			width: "690px",
			overflow: "auto",
			background: "#f1f1f1",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Firma
				</p>
				<div className="flexSpace"/>
				
			</div>
			
			<div style={{
				padding: 5,
				borderRadius: 13,
				border:"1px solid #999",
				margin: "5px 2px "
			}}>
				<SignatureCanvas penColor='#090909'
					canvasProps={{width: 650, height: 200, className: 'sigCanvas'}}
					ref={sigPad} />
			</div>
			<div className="_dsplFlx " style={{marginBottom:25}}>
				<p className="group_title addBtn" onClick={clear}>
					Clear
				</p>
				<div className="flexSpace"/>
				<p className="group_title purple addBtn" onClick={save}>
					Guardar
				</p>
			</div>
			
		
		</div>
	)
}

export default SignatureBox;