import React, { useEffect, useState, useRef } from "react";



import { getStore, gen16CodeId, ObjectKeys,  parseDate, updStore, _monthNames, copyToClipboard, parseDateMMDDYYYY } from "../../../stores/Utils";



import "./dashboard.css"
import { UpdClient_Cmpt, AddressDashboard_Cmpt, EducationDashboard_Cmpt, JobsDashboard_Cmpt, EntryHistoryDashboard_Cmpt, PassportRecordDashboard_Cmpt, ParentsDashboard_Cmpt } from "..";
import { MarriageCertification_Cmpt } from "../../MarriageCertificate";
import { BirthCertification_Cmpt, TranslateDL_Cmpt } from "../../BirthCerticate";
import { OpenModal } from "../../../components/DialogHRM";
import { TabsIconHRM_Cmpt, DropDwnPicker_Cmpt } from "../../../components";
import SignatureBox from "./SignatureBox";
import { generatePD, generatePD134, fillInformationBiometricAppointment } from "../../BirthCerticate/services";
import { get_print_structure } from "../services/fetch_service";

const UpdClient = UpdClient_Cmpt();

const MarriageCertification = MarriageCertification_Cmpt();
const BirthCertification = BirthCertification_Cmpt();

const TranslateDL = TranslateDL_Cmpt();



const TabsIconHRM = TabsIconHRM_Cmpt();

const AddressDashboard = AddressDashboard_Cmpt();

const EducationDashboard = EducationDashboard_Cmpt();

const JobsDashboard = JobsDashboard_Cmpt();

const EntryHistoryDashboard = EntryHistoryDashboard_Cmpt();

const PassportRecordDashboard = PassportRecordDashboard_Cmpt();

const ParentsDashboard = ParentsDashboard_Cmpt();

const DropDwnPicker = DropDwnPicker_Cmpt();





const frmNm = "upd_client"






const DetailClient  = ({   close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [view, set_view] = useState(1);

	const [mctView, set_mctView] = useState(false);
	const [bctView, set_bctView] = useState(false);



	const openUpdView =  () => {

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<UpdClient confirm={closeDetails}/>
		
		OpenModal(modal_data);
	}
	
	
	

	const closeDetails = ()=>{
		updStore("upd_client",null);
		close(false);
		window.scrollTo(0,0)
	}


	const closeViews = ()=>{
		set_bctView(false)
		set_mctView(false);
	}

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
		}
	});


	
	
	const handleSignature =  (v) => {
		updStore("b64_signature",v);
	}

	const getSignature =  (f) => {
		
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <SignatureBox onConfirm={handleSignature}/>
		OpenModal(dataModal);
	}
	const printPD  = async ()  => {
		let pdfUri = await generatePD({}, true, true);
		window.open(pdfUri);
	}



	const birthViewModal =  (f) => {
		updStore("birth_certificate", null);
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <BirthCertification onConfirm={handleSignature}/>
		OpenModal(dataModal);
	}


	const marriageViewModal =  (f) => {
		updStore("marriage_certificate", null);
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <MarriageCertification onConfirm={handleSignature}/>
		OpenModal(dataModal);
	}

	const translateDLViewModal =  (f) => {
		updStore("translate_dl", null);
		updStore("driver_licence_translate", null);
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <TranslateDL onConfirm={handleSignature}/>
		OpenModal(dataModal);
	}

	

	const printIBA  = async ()  => {


		let user = {...getStore("upd_client")};

		let dob = (new Date(user?.dateOfBirth));
		let MM = dob.getMonth()+1;
		let frm_userInfo = {
			dobMonth: _monthNames["en"][MM],
			dobYear:dob.getFullYear(),
			dobDay: dob.getDate(),
			firstName:user?.firstName,
			middleName:user?.middleName?user?.middleName:"",
			lastName: user?.lastName,
			height: user?.height,
			weight: user?.weight,
		}
		let pdfUri = await fillInformationBiometricAppointment(frm_userInfo, true, true);
		window.open(pdfUri);
	}

	

	const printPD134  = async ()  => {
		let pdfUri = await generatePD134({}, true, true);
		window.open(pdfUri);
	}



	const handleOnSel = (v) =>{

		if(v==="1"){
			printPD();
		}
		else if(v==="2"){
			printPD134();
		}
		else if(v==="3"){
			printIBA();
		}
	}




	return (
		<div className={"detailClient"}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Detalles
				</p>
				<div className="flexSpace"/>
				
				<p className="group_title addBtn" onClick={()=>closeDetails(false)}>
					cerrar
				</p>
			</div>
			

			<TabsIconHRM 
				indexTab={view} 
				data={[
					{key:1,label:"Personal", icon:"account"}, 
					{key:2,label:"Familia", icon:"parents"}, 
					{key:3,label:"Direcciones", icon:"home"}, 
					{key:4,label:"Educacion", icon:"school_outline"}, 
					{key:5,label:"Empleos", icon:"warehouse"},
					{key:6,label:"Entradas", icon:"police-badge-outline"},
					{key:7,label:"Passport", icon:"passport"},
					{key:8,label:"pdf", icon:"pdf_box"},
					
				]} 
				updateIndex={set_view}  
			/>
			


			<OpacityView viewId={1} activeView={view}>
				<div>
					<div className="_dsplFlx ">
						<p className="group_title">
							Informacion Personal
						</p>
						<div className="flexSpace"/>
						<button className="group_title addBtn purple" onClick={()=>openUpdView(true)}>
							Actualizar
						</button>
					</div>

					<div className="_dsplFlx spaceAround flxWrp"> 
						<TextView  
							init={getStore(frmNm)?.["genre"]} 
							label={"Sexo"} 
							width={120} 
						/>
						<div className="flexSpace"/>
						<TextView
							init={getStore(frmNm)?.["firstName"]} 
							label={"Nombre"} 
							width={240} 
						/>
						<div className="flexSpace"/>
						<TextView  
							init={getStore(frmNm)?.["middleName"]} 
							label={"Segundo Nombre"} 
							width={240} 
						/>
						<div className="flexSpace"/>
						<TextView  
							init={getStore(frmNm)?.["lastName"]} 
							label={"Apellidos"} 
							width={360} 
						/>
						<div className="flexSpace"/>
						<TextView  
							init={getStore(frmNm)?.["dateOfBirth"] ? parseDate(getStore(frmNm)?.["dateOfBirth"]): ""} 
							label={"Fecha de Nacimiento"} 
							width={160}
							value2copy={parseDateMMDDYYYY(getStore(frmNm)?.["dateOfBirth"])}
						/>
						
						<div className="flexSpace"/>
						<TextView  
							init={getStore(frmNm)?.["race"]} 
							label={"race"} 
							width={180} 
						/>
						<div className="flexSpace"/>
						<TextView  
							init={getStore(frmNm)?.["ss"]} 
							label={"Social Number"} 
							width={120} 
						/>
						<div className="flexSpace"/>
						<TextView  
							init={getStore(frmNm)?.["alienNumber"]} 
							label={"Alien Number"} 
							width={120} 
						/>
						<div className="flexSpace"/>
						<TextView  
							init={getStore(frmNm)?.["phoneNumber"]} 
							label={"Telefono"} 
							width={120} 
						/>
						<div className="flexSpace"/>
						
					</div>
					<div className="separator"/>
					<p className="group_title">
						Lugar de Nacimiento 
					</p>
					<div className="_dsplFlx spaceAround flxWrp"> 
						<TextView 
							init={getStore(frmNm)?.["placeOfBirth"]?.["city"]} 
							label={"Municipio"} 
							width={240} 
						/>
						<TextView  
							init={getStore(frmNm)?.["placeOfBirth"]?.["state"]} 
							label={"Provincia"} 
							width={240} 
						/>
					</div>
				</div>
			</OpacityView>

			
			
			<OpacityView viewId={2} activeView={view}>
				<ParentsDashboard />
			</OpacityView>

			<OpacityView viewId={3} activeView={view}>
				<AddressDashboard />
			</OpacityView>

			<OpacityView viewId={4} activeView={view}>
				<EducationDashboard />
			</OpacityView>

			<OpacityView viewId={5} activeView={view}>
				<JobsDashboard />
			</OpacityView>

			<OpacityView viewId={6} activeView={view}>
				<EntryHistoryDashboard />
			</OpacityView>
			<OpacityView viewId={7} activeView={view}>
				<PassportRecordDashboard />
			</OpacityView>
			<OpacityView viewId={8} activeView={view}>
				<div>
					<p className="group_title">
						Documentos a Imprimir
					</p>
					<div className="_dsplFlx ">
						<p className="form_title">
						
						</p>
						<div className="flexSpace"/>
						
						<p className="group_title addBtn purple" onClick={()=>getSignature(false)}>
							Signature
						</p>
						<div style={{marginTop:9}}>
							<DropDwnPicker 
								lbl_empty={"Forms to Print"} 
								list={[
									{label:"Preparer Signature", id:"1"},
									{label:"Preparer Signature I-134", id:"2"},
									{label:"Biometric Info", id:"3"}
								]} 
								onSelect={handleOnSel}
								direction={"right"}
							/>
						</div>	
						
					</div>
					<div className="_dsplFlx flxWrp">
						<div className="card">
							<button onClick={()=>birthViewModal()}>
								Certificado Nacimiento
							</button>
						</div>
						<div className="flexSpace"/>
						<div className="card">
							<button onClick={()=>marriageViewModal()}>
								Certificado Matrimonio
							</button>
						</div>
						<div className="flexSpace"/>
						<div className="card">
							<button onClick={()=>translateDLViewModal()}>
								Traducion de Licencia
							</button>
						</div>
						
					</div>
					<div>
					{mctView?
					<MarriageCertification close={closeViews}/>
					:bctView?
					<BirthCertification close={closeViews}/>:null
					}

					</div>
				</div>
			</OpacityView>
			
		

			
			
			



		</div>
	);
};

export default DetailClient;


const compareJSON = (fls,obj, bkUp) =>{
	let r = 0;
	let data = {}
	for(let i in fls){
		let k1 = fls[i];
		if(obj[k1] ){
			if(typeof obj[k1] === "object"){
				ObjectKeys(obj[k1]).map(fk2=>{
					if(obj?.[k1]?.[fk2]!==bkUp?.[k1]?.[fk2]){
						r = 1;
						data[k1] = obj[k1];
					}
				})
			}
			else if(obj[k1] &&  obj[k1]!==bkUp[k1]){
				r = 1;
				data[k1] = obj[k1];
			}
		}
	}
	return {data, hasChange:r};
  }




  

const TextView  = ({ init, label, width, value2copy }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);


	const updObs = ()=>{
		set_obs(gen16CodeId())
	}


	

	
	const copyToClpb  =  ()  => {

		copyToClipboard( value2copy ? value2copy: init,updObs);
	};
	

		
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updObs();
		}
	});

	if(!init){
		return null
	}

	return (
		<div onClick={copyToClpb}>
			<p className="label">
				{label}
			</p>
			<div  
				style={width? {width: width}: null}
				className="r7gAOb yyJm8b"
			>
				{init}
			</div>
		</div>
	)
}


const OpacityView  = ({ viewId, activeView, children }) => {
	return(
		<div className={"opacityView"} style={{opacity : viewId=== activeView?1:0, marginTop: 29 }}>
			{viewId=== activeView?children:null}
		</div>
	)

}